<template>
	<div class="row">
		<!--new pizza-->
		<div class="col-sm-12 col-lg-8">
			<app-new-pizza></app-new-pizza>
		</div>
		<!--品种展示-->
		<div class="col-sm-12 col-lg-4">
			<h3 class="text-muted my-5">菜单</h3>
			<table class="table">
				<thead class="table-default">
					<tr>
						<td>品种</td>
						<td>删除</td>
					</tr>
				</thead>
				<tbody v-for="item in getMenuItems" :key="item.name">
					<Tr>
						<Td>{{item.name}}</Td>
						<td>
							<button @click="deleteData(item)" class="btn btn-outline-danger btn-sm">&times</button>
						</td>
					</Tr>
				</tbody>
			</table>
		</div>
	</div>
</template>

<script>
	import newPizza from './NewPizza.vue'
	
	export default{
		data(){
			return{
//				getMenuItems:[]
			}
		},
		
		//进入组件之前
//		beforeRouteEnter:(to,from,next)=>{
//			next(vm => {
//				alert("Hello " + vm.name);
//			})
//		}
		
		//离开组件之后
//		beforeRouteLeave:(to,from,next)=>{
//			if(confirm("确认离开吗？" == true)){
//				next()
//			}else{
//				next(false)
//			}
//		}

		components:{
			'app-new-pizza':newPizza
		},
		computed:{
			getMenuItems:{
				//在vuex中获取数据
				get(){
					//return this.$store.state.menuItems
					
					//通过getters获取数据
					return this.$store.getters.getMenuItems
				},
				set(){}
			}
		},
		created(){
			fetch('https://wd3684281753hlbnai.wilddogio.com/menu.json')
			     .then(res => {
			     	return res.json()
			     })
			     .then(data => {
			     	let menuArray = []
			     	for(let key in data){
			     		//console.log(data[key])
			     		data[key].id = key
			     		menuArray.push(data[key])
			     	}
                    //this.getMenuItems = menuArray
                    
					//数据同步
					this.$store.commit('setMenuItems',menuArray)
			     })
		},
		methods:{
			deleteData(item){
				fetch("https://wd3684281753hlbnai.wilddogio.com/menu/"+item.id+"/.json",{
		          method:"DELETE",
		          headers:{
		            'Content-type':'application/json'
		          }
		        })
				.then(res => res.json())
//				.then(data => this.$router.push({name:"meauLink"}))
				.then(data => {
					this.$store.commit('removeMenuItems',item)
				})
				.catch(err => console.log(err))
			}
		}
	}
</script>

<style>
</style>


