<template>
	<div class="row">
		<div id="home" class="col-sm-12 text-center">
			<div class="background">
				<h1>欢迎大家平常PIZZA!</h1>
				<h2>欢迎大家平常PIZZA!</h2>
				<button @click="goToMeau" class="btn btn-success">Let's order!</button>
			</div>
		</div>
		
		
	</div>
</template>

<script>
export default {
	name:'home',
	methods:{
		goToMeau(){
			//跳转到上一次浏览的页面
			//this.$router.go(-1)
			
			//指定跳转的地址
			//this.$router.replace('/meau')
			
			//指定跳转路由的名字下
			//this.$router.replace({name:'meauLink'})
			
			//通过push进行跳转 （常用）
			//this.$router.push('/meau')
			this.$router.push({name:'meauLink'})
		}
	}
}
</script>

<style>
#home{
	background: url('../../src/assets/pizza.jpg') no-repeat;
	height: 85vh;
	padding: 10%;
}

h1,h2{
	margin: 6%;
}

.background{ 
	background: #eee;
	opacity: 0.8; 
	max-width: 70vw;
	margin: 0 auto;
	padding: 20px 0;
}
</style>


